.projects {
    max-width: 80%;
    margin: 0 auto;
    text-align: start;
    font-size: 1.2em;
}

.projects-header {
    font-weight: bold;
    margin: 0.5em 0;
    margin-bottom: 6em;
    display: flex;
}

.projects-title {
    font-family: "Google Sans Bold", sans-serif;
    font-size: 1.8em;
    color: var(--color-primary);
    margin-top: 20px;
    line-height: 1.1;
    margin-bottom: 10px;
}

.projects-reference {
    font-family: "Google Sans Medium", sans-serif;
    line-height: 1.2;
    display: inline-flex;
    font-size: 1em;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    width: fit-content;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: var(--color-secondary);
    color: #f5f5f5;
    border: 0px;;
    border-radius: 10px;
    transition: 0.5s;
}

.projects-reference:hover {
    background-color: rgb(141, 138, 138);
}

.projects-reference i {
    font-size: 0.8em;
    height: fit-content;
    margin-right: 10px;
}

.projects-reference p {
    font-size: 0.5em;
    margin-bottom: 0;
    margin-top: 0;
}

.projects-header-text, .projects-header-image {
    flex: 1;
}

.projects-header-text {
    margin-left: 2.5em;
}

.projects-header-image img {
    width: 100%; 
    height: auto;
}

.projects-content p {
    line-height: 1.5;
    margin-top: 1em;
}

.projects-subtitle {
    font-family: "Google Sans Medium", sans-serif;
    font-weight: 400;
    font-size: 1.1em;
    margin-top: 15px;
    color: var(--color-tertiary);
    line-height: 1.2;
}

.projects-header-text, .projects-header-img {
    flex: 1;
}

.projects-content {
    display: flex;
    flex-direction: row;
}


@media (max-width: 48em) {
    .projects {
        font-size: 1.5em;
        text-align: center;
    }

    .projects-header {
        display: block;
        margin-bottom: 2.5em;
    }

    .projects-header-text {
        margin-left: 0;
    }

    .projects-reference p {
        font-size: 8px;
    }

    .projects-reference i {
        margin-right: 5px;
    }

    .projects-reference {
        padding-top: 5px;
        padding-bottom: 5px;
        padding-left: 10px;
        padding-right: 10px;
    }

    .projects-header-image {
        margin-top: 2em;
    }

    .projects-content {
        display: block;
    }
}